
import * as sqls from "./sqls";
import { first } from "~/core/database";

export const get = async params => {
  const { name } = params;
  let { from, to } = params;

  if (!from) from = new Date(2000, 1, 1);
  if (!to) to = new Date(3000, 1, 1);

  const sql = JSON.parse(JSON.stringify(sqls))[name];
  const res = await first(sql, [from, to]);

  return res;
};
